import React from 'react';
import './Dashboard.css';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

function TrafficOverview() {
  const trafficData = [
    { site: 'github.com', visits: 145, time: '12h 30m' },
    { site: 'stackoverflow.com', visits: 98, time: '8h 15m' },
    { site: 'chat.openai.com', visits: 85, time: '6h 45m' },
    { site: 'youtube.com', visits: 76, time: '5h 20m' },
    { site: 'linkedin.com', visits: 65, time: '4h 10m' }
  ];

  return (
    <div className="dashboard-card">
      <h3 className="card-title">Traffic Overview</h3>
      <div className="traffic-list">
        {trafficData.map((item, index) => (
          <div key={index} className="traffic-item">
            <div className="traffic-info">
              <span className="site-name">{item.site}</span>
              <span className="visit-count">{item.visits} visits</span>
            </div>
            <span className="time-spent">{item.time}</span>
          </div>
        ))}
      </div>
      <div className="report-chart">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart 
            data={trafficData} 
            margin={{ top: 10, right: 10, left: 0, bottom: 0 }}
            layout="vertical"
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              type="number"
              axisLine={{ stroke: 'var(--border-color)' }}
              tick={{ fill: 'var(--text-secondary)' }}
            />
            <YAxis 
              type="category"
              dataKey="site"
              axisLine={{ stroke: 'var(--border-color)' }}
              tick={{ fill: 'var(--text-secondary)' }}
            />
            <Tooltip 
              formatter={(value) => `${value} visits`}
              contentStyle={{
                background: 'var(--secondary-bg)',
                border: '1px solid var(--border-color)',
                borderRadius: '4px',
                color: 'var(--text-primary)'
              }}
            />
            <Bar 
              dataKey="visits" 
              fill="#3B82F6"
              radius={[0, 4, 4, 0]}
            />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}

export default TrafficOverview; 