import React from 'react';
import './Productivity.css';

function Recommendations() {
  const recommendations = [
    {
      title: 'Reduce Social Media Time',
      description: 'Your social media usage has increased by 25% this week',
      type: 'warning'
    },
    {
      title: 'Great Work Balance',
      description: 'You maintained a good ratio of work to break time',
      type: 'success'
    },
    {
      title: 'Set Entertainment Limits',
      description: 'Consider setting limits for entertainment websites',
      type: 'info'
    }
  ];

  return (
    <div className="productivity-card">
      <h3 className="card-title">Recommendations</h3>
      <div className="recommendations-list">
        {recommendations.map((rec, index) => (
          <div key={index} className={`recommendation-item ${rec.type}`}>
            <div className="recommendation-icon">
              {rec.type === 'warning' && '⚠️'}
              {rec.type === 'success' && '✅'}
              {rec.type === 'info' && 'ℹ️'}
            </div>
            <div className="recommendation-content">
              <h4 className="recommendation-title">{rec.title}</h4>
              <p className="recommendation-description">{rec.description}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Recommendations; 