import React from 'react';
import './Reports.css';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

function TrendsAnalysis() {
  const trendsData = [
    { category: 'Work', hours: 45, change: '+15%' },
    { category: 'Social', hours: 28, change: '-8%' },
    { category: 'Entertainment', hours: 32, change: '+2%' },
    { category: 'Learning', hours: 25, change: '+10%' },
    { category: 'Health', hours: 20, change: '-5%' }
  ];

  return (
    <div className="report-card">
      <h3 className="card-title">Usage Trends</h3>
      <div className="trends-list">
        {trendsData.map((trend, index) => (
          <div key={index} className="trend-item">
            <span className="trend-category">{trend.category}</span>
            <span className={`trend-change ${getChangeClass(trend.change)}`}>
              {trend.change}
            </span>
          </div>
        ))}
      </div>
      <div className="report-chart">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart data={trendsData} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="category" 
              axisLine={{ stroke: 'var(--border-color)' }}
              tick={{ fill: 'var(--text-secondary)' }}
            />
            <YAxis 
              axisLine={{ stroke: 'var(--border-color)' }}
              tick={{ fill: 'var(--text-secondary)' }}
            />
            <Tooltip 
              formatter={(value) => `${value}h`}
              contentStyle={{
                background: 'var(--secondary-bg)',
                border: '1px solid var(--border-color)',
                borderRadius: '4px',
                color: 'var(--text-primary)'
              }}
            />
            <Bar 
              dataKey="hours" 
              fill="#3B82F6"
              radius={[4, 4, 0, 0]}
            />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}

function getChangeClass(change) {
  if (change.startsWith('+')) return 'increasing';
  if (change.startsWith('-')) return 'decreasing';
  return 'stable';
}

export default TrendsAnalysis; 