import React from 'react';
import './Reports.css';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

function MonthlyReport() {
  const monthlyData = [
    { date: 'Week 1', hours: 35 },
    { date: 'Week 2', hours: 42 },
    { date: 'Week 3', hours: 38 },
    { date: 'Week 4', hours: 41 }
  ];

  return (
    <div className="report-card">
      <h3 className="card-title">Monthly Overview</h3>
      <div className="report-stats">
        <div className="report-stat">
          <span className="stat-label">Total Time</span>
          <span className="stat-value">156h 30m</span>
          <span className="stat-change negative">-4h from last month</span>
        </div>
        <div className="report-stat">
          <span className="stat-label">Most Active Week</span>
          <span className="stat-value">Week 2</span>
          <span className="stat-detail">42h 15m</span>
        </div>
      </div>
      <div className="report-chart">
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart data={monthlyData} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
            <defs>
              <linearGradient id="colorHours" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#3B82F6" stopOpacity={0.2}/>
                <stop offset="95%" stopColor="#3B82F6" stopOpacity={0}/>
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="date" 
              axisLine={{ stroke: 'var(--border-color)' }}
              tick={{ fill: 'var(--text-secondary)' }}
            />
            <YAxis 
              axisLine={{ stroke: 'var(--border-color)' }}
              tick={{ fill: 'var(--text-secondary)' }}
            />
            <Tooltip 
              formatter={(value) => `${value}h`}
              contentStyle={{
                background: 'var(--secondary-bg)',
                border: '1px solid var(--border-color)',
                borderRadius: '4px',
                color: 'var(--text-primary)'
              }}
            />
            <Area 
              type="monotone" 
              dataKey="hours" 
              stroke="#3B82F6" 
              fill="url(#colorHours)" 
              strokeWidth={2}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}

export default MonthlyReport; 