import React from 'react';
import './Productivity.css';

function TimeLimits() {
  const limits = [
    { category: 'Social', limit: '2h', used: '1h 30m' },
    { category: 'Entertainment', limit: '3h', used: '2h 45m' },
  ];

  return (
    <div className="productivity-card">
      <h3 className="card-title">Daily Limits</h3>
      <div className="limits-list">
        {limits.map((limit, index) => (
          <div key={index} className="limit-item">
            <div className="limit-info">
              <span className="limit-category">{limit.category}</span>
              <span className="limit-time">{limit.used} / {limit.limit}</span>
            </div>
            <div className="limit-progress">
              <div 
                className="progress-bar" 
                style={{ width: `${(parseFloat(limit.used) / parseFloat(limit.limit)) * 100}%` }}
              />
            </div>
          </div>
        ))}
      </div>
      <button className="add-limit-button">
        + Set New Limit
      </button>
    </div>
  );
}

export default TimeLimits; 