import React from 'react';
import './Productivity.css';

function CategoryManager() {
  const categories = [
    { name: 'Work', color: '#10B981', sites: ['github.com', 'gitlab.com'] },
    { name: 'Social', color: '#3B82F6', sites: ['twitter.com', 'facebook.com'] },
    { name: 'Entertainment', color: '#F59E0B', sites: ['youtube.com', 'netflix.com'] },
  ];

  return (
    <div className="productivity-card">
      <h3 className="card-title">Categories</h3>
      <div className="categories-list">
        {categories.map((category, index) => (
          <div key={index} className="category-item">
            <div className="category-header">
              <div className="category-color" style={{ backgroundColor: category.color }} />
              <span className="category-name">{category.name}</span>
              <button className="category-edit">Edit</button>
            </div>
            <div className="category-sites">
              {category.sites.map((site, siteIndex) => (
                <span key={siteIndex} className="site-tag">{site}</span>
              ))}
            </div>
          </div>
        ))}
      </div>
      <button className="add-category-button">
        + Add Category
      </button>
    </div>
  );
}

export default CategoryManager; 