import React from 'react';
import './Subscription.css';

function PricingPlans() {
  const plans = [
    {
      name: 'Free',
      price: '$0',
      period: 'forever',
      features: [
        'Basic time tracking',
        'Limited to 3 categories',
        'Basic reports',
        '7-day history'
      ]
    },
    {
      name: 'Pro',
      price: '$9.99',
      period: 'per month',
      features: [
        'Advanced time tracking',
        'Unlimited categories',
        'Detailed analytics',
        'Unlimited history',
        'Priority support'
      ],
      recommended: true
    },
    {
      name: 'Team',
      price: '$29.99',
      period: 'per month',
      features: [
        'Everything in Pro',
        'Team management',
        'Team analytics',
        'API access',
        'Custom reports'
      ]
    }
  ];

  return (
    <div className="subscription-card">
      <h3 className="card-title">Available Plans</h3>
      <div className="plans-grid">
        {plans.map((plan, index) => (
          <div key={index} className={`plan-card ${plan.recommended ? 'recommended' : ''}`}>
            {plan.recommended && <div className="recommended-badge">Recommended</div>}
            <div className="plan-header">
              <h4 className="plan-name">{plan.name}</h4>
              <div className="plan-pricing">
                <span className="price">{plan.price}</span>
                <span className="period">{plan.period}</span>
              </div>
            </div>
            <ul className="plan-features">
              {plan.features.map((feature, i) => (
                <li key={i}>{feature}</li>
              ))}
            </ul>
            <button className={`select-plan-button ${plan.recommended ? 'primary' : ''}`}>
              Select Plan
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}

export default PricingPlans; 