import React from 'react';
import './Settings.css';

function SiteExclusions() {
  const excludedSites = [
    'mail.google.com',
    'drive.google.com',
    'docs.google.com'
  ];

  return (
    <div className="settings-card">
      <h3 className="card-title">Site Exclusions</h3>
      <div className="exclusions-list">
        {excludedSites.map((site, index) => (
          <div key={index} className="exclusion-item">
            <span className="site-name">{site}</span>
            <button className="remove-button">Remove</button>
          </div>
        ))}
      </div>
      <div className="add-exclusion">
        <input 
          type="text" 
          className="exclusion-input" 
          placeholder="Enter site URL"
        />
        <button className="add-button">Add Site</button>
      </div>
    </div>
  );
}

export default SiteExclusions; 