import React from 'react';
import './Dashboard.css';

function TopSites() {
  const sites = [
    { name: 'github.com', time: '1h 30m', icon: '🌐' },
    { name: 'stackoverflow.com', time: '45m', icon: '🌐' },
    { name: 'chat.openai.com', time: '30m', icon: '🌐' },
    { name: 'google.com', time: '25m', icon: '🌐' },
  ];

  return (
    <div className="dashboard-card">
      <h3 className="card-title">Top Sites</h3>
      <div className="sites-list">
        {sites.map((site, index) => (
          <div key={index} className="site-item">
            <span className="site-icon">{site.icon}</span>
            <span className="site-name">{site.name}</span>
            <span className="site-time">{site.time}</span>
          </div>
        ))}
      </div>
    </div>
  );
}

export default TopSites; 