import React, { useState } from 'react';
import DateRangePicker from '../components/statistics/DateRangePicker';
import SitesList from '../components/statistics/SitesList';
import StatsFilters from '../components/statistics/StatsFilters';
import ExportData from '../components/statistics/ExportData';
import './Pages.css';

function Statistics() {
  const [dateRange, setDateRange] = useState({ start: null, end: null });
  const [filters, setFilters] = useState({});

  return (
    <div className="page-container">
      <div className="page-header">
        <h1>Statistics</h1>
        <p className="page-description">Detailed analysis of your browsing data</p>
      </div>
      <div className="stats-controls">
        <DateRangePicker onChange={setDateRange} />
        <StatsFilters onChange={setFilters} />
        <ExportData />
      </div>
      <div className="page-content">
        <SitesList dateRange={dateRange} filters={filters} />
      </div>
    </div>
  );
}

export default Statistics; 