import React, { useState, useEffect, useRef } from 'react';
import { useAuth } from '../../context/AuthContext';
import { supabase } from '../../config/supabase';
import './LiveChat.css';

function LiveChat({ isOpen, onClose, onUnreadCountChange }) {
  const { user, profile } = useAuth();
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const messagesEndRef = useRef(null);
  const [loading, setLoading] = useState(true);
  const channelRef = useRef(null);
  const [lastReadTimestamp, setLastReadTimestamp] = useState(localStorage.getItem('lastReadTimestamp') || Date.now());

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    loadMessages(); // Загружаем сообщения при монтировании компонента
    setupRealtimeSubscription();

    return () => {
      if (channelRef.current) {
        channelRef.current.unsubscribe();
      }
    };
  }, []); // Пустой массив зависимостей

  useEffect(() => {
    if (isOpen) {
      setLastReadTimestamp(Date.now());
      localStorage.setItem('lastReadTimestamp', Date.now());
      onUnreadCountChange(0);
    }
  }, [isOpen]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const loadMessages = async () => {
    try {
      const { data: messages, error } = await supabase
        .from('chat_messages')
        .select(`
          id,
          content,
          user_id,
          user_name,
          created_at,
          profiles!user_id (
            display_name,
            avatar_url
          )
        `)
        .order('created_at', { ascending: true })
        .limit(100);

      if (error) throw error;

      console.log('Loaded messages:', messages); // Для отладки

      // Преобразуем данные в нужный формат
      const formattedMessages = messages?.map(msg => ({
        ...msg,
        profiles: msg.profiles || {
          display_name: msg.user_name,
          avatar_url: null
        }
      })) || [];

      setMessages(formattedMessages);
    } catch (error) {
      console.error('Error loading messages:', error);
    } finally {
      setLoading(false);
    }
  };

  const setupRealtimeSubscription = async () => {
    try {
      if (channelRef.current) {
        await channelRef.current.unsubscribe();
      }

      const channel = supabase.channel('chat_messages_channel', {
        config: {
          broadcast: { self: true },
          presence: { key: user.id },
        },
      });

      channel
        .on('broadcast', { event: 'new_message' }, async ({ payload }) => {
          const { data: profileData } = await supabase
            .from('profiles')
            .select('display_name, avatar_url')
            .eq('id', payload.user_id)
            .single();

          setMessages(prev => [...prev, {
            ...payload,
            profiles: profileData
          }]);
          updateUnreadCount(payload);
        })
        .subscribe();

      channelRef.current = channel;
    } catch (error) {
      console.error('Error setting up realtime subscription:', error);
    }
  };

  const sendMessage = async (e) => {
    e.preventDefault();
    if (!newMessage.trim()) return;

    try {
      const { error: dbError } = await supabase
        .from('chat_messages')
        .insert([
          {
            user_id: user.id,
            content: newMessage.trim(),
            user_name: profile?.display_name || user.email
          }
        ]);

      if (dbError) throw dbError;

      if (channelRef.current) {
        await channelRef.current.send({
          type: 'broadcast',
          event: 'new_message',
          payload: {
            id: Date.now(),
            user_id: user.id,
            content: newMessage.trim(),
            user_name: profile?.display_name || user.email,
            created_at: new Date().toISOString(),
            profiles: {
              display_name: profile?.display_name,
              avatar_url: profile?.avatar_url
            }
          }
        });
      }

      setNewMessage('');
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  const updateUnreadCount = (newMessage) => {
    if (!isOpen && newMessage.created_at > lastReadTimestamp) {
      onUnreadCountChange(prev => prev + 1);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="chat-overlay">
      <div className="chat-container">
        <div className="chat-header">
          <h3>Живой чат</h3>
          <button className="chat-close" onClick={onClose}>×</button>
        </div>
        
        <div className="chat-messages">
          {loading ? (
            <div className="chat-loading">Загрузка сообщений...</div>
          ) : messages.length === 0 ? (
            <div className="chat-empty">Нет сообщений</div>
          ) : (
            messages.map(message => (
              <div 
                key={message.id} 
                className={`chat-message ${message.user_id === user.id ? 'own-message' : ''}`}
              >
                <div className="message-avatar">
                  {message.profiles?.avatar_url ? (
                    <img src={message.profiles.avatar_url} alt="Avatar" />
                  ) : (
                    <div className="avatar-placeholder">
                      {(message.profiles?.display_name || message.user_name || 'U')[0].toUpperCase()}
                    </div>
                  )}
                </div>
                <div className="message-content">
                  <div className="message-author">
                    {message.profiles?.display_name || message.user_name}
                  </div>
                  <div className="message-text">{message.content}</div>
                  <div className="message-time">
                    {new Date(message.created_at).toLocaleTimeString()}
                  </div>
                </div>
              </div>
            ))
          )}
          <div ref={messagesEndRef} />
        </div>

        <form className="chat-input" onSubmit={sendMessage}>
          <input
            type="text"
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            placeholder="Написать сообщение..."
            maxLength={500}
          />
          <button type="submit" disabled={!newMessage.trim()}>
            Отправить
          </button>
        </form>
      </div>
    </div>
  );
}

export default LiveChat; 