import React from 'react';
import './Reports.css';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

function WeeklyReport() {
  const weeklyData = [
    { date: 'Mon', hours: 5 },
    { date: 'Tue', hours: 7 },
    { date: 'Wed', hours: 8.5 },
    { date: 'Thu', hours: 6 },
    { date: 'Fri', hours: 4 },
    { date: 'Sat', hours: 2 },
    { date: 'Sun', hours: 3 }
  ];

  return (
    <div className="report-card">
      <h3 className="card-title">Weekly Summary</h3>
      <div className="report-stats">
        <div className="report-stat">
          <span className="stat-label">Total Time</span>
          <span className="stat-value">38h 15m</span>
          <span className="stat-change positive">+2.5h from last week</span>
        </div>
        <div className="report-stat">
          <span className="stat-label">Most Active Day</span>
          <span className="stat-value">Wednesday</span>
          <span className="stat-detail">8h 45m</span>
        </div>
      </div>
      <div className="report-chart">
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart data={weeklyData} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
            <defs>
              <linearGradient id="colorWeekly" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#3B82F6" stopOpacity={0.2}/>
                <stop offset="95%" stopColor="#3B82F6" stopOpacity={0}/>
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="date" />
            <YAxis />
            <Tooltip formatter={(value) => `${value}h`} />
            <Area 
              type="monotone" 
              dataKey="hours" 
              stroke="#3B82F6" 
              fill="url(#colorWeekly)" 
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}

export default WeeklyReport; 