import React, { useState } from 'react';
import { useAuth } from '../context/AuthContext';
import { supabase } from '../config/supabase';
import './Pages.css';
import { useToast } from '../context/ToastContext';

function Account() {
  const { user, profile, refreshProfile } = useAuth();
  const { addToast } = useToast();
  const [uploading, setUploading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [activeSection, setActiveSection] = useState('profile');
  const [settings, setSettings] = useState({
    display_name: profile?.display_name || '',
    bio: profile?.bio || '',
    time_zone: profile?.time_zone || 'UTC',
    daily_goal: profile?.daily_goal || 480,
    notification_settings: profile?.notification_settings || {
      daily_summary: true,
      weekly_report: true,
      goal_alerts: true,
      idle_detection: 15
    },
    theme: profile?.theme || 'dark',
    language: profile?.language || 'en'
  });

  const handleSettingChange = (key, value) => {
    if (key.includes('.')) {
      const [parent, child] = key.split('.');
      setSettings(prev => ({
        ...prev,
        [parent]: {
          ...prev[parent],
          [child]: value
        }
      }));
    } else {
      setSettings(prev => ({ ...prev, [key]: value }));
    }
  };

  const handleSaveSettings = async () => {
    try {
      setSaving(true);

      const { error } = await supabase
        .from('profiles')
        .update(settings)
        .eq('id', user.id);

      if (error) throw error;

      await refreshProfile();
      addToast('Настройки успешно сохранены', 'success');
    } catch (err) {
      console.error('Error saving settings:', err);
      addToast('Ошибка при сохранении настроек', 'error');
    } finally {
      setSaving(false);
    }
  };

  const handleAvatarChange = async (event) => {
    try {
      setUploading(true);

      const file = event.target.files[0];
      if (!file) return;

      // Проверяем размер и тип файла
      if (file.size > 2 * 1024 * 1024) {
        throw new Error('Файл слишком большой. Максимальный размер 2MB');
      }
      if (!file.type.startsWith('image/')) {
        throw new Error('Пожалуйста, выберите изображение');
      }

      // Генерируем уникальное имя файла
      const fileExt = file.name.split('.').pop();
      const fileName = `${user.id}-${Math.random()}.${fileExt}`;
      const filePath = `avatars/${fileName}`;

      // Загружаем файл
      const { error: uploadError } = await supabase.storage
        .from('avatars')
        .upload(filePath, file);

      if (uploadError) throw uploadError;

      // Получаем публичную ссылку
      const { data: { publicUrl }, error: urlError } = supabase.storage
        .from('avatars')
        .getPublicUrl(filePath);

      if (urlError) throw urlError;

      // Обновляем профиль
      const { error: updateError } = await supabase
        .from('profiles')
        .update({ avatar_url: publicUrl })
        .eq('id', user.id);

      if (updateError) throw updateError;

      await refreshProfile();
      addToast('Аватар успешно обновлен', 'success');
    } catch (err) {
      console.error('Error uploading avatar:', err);
      addToast(err.message, 'error');
    } finally {
      setUploading(false);
    }
  };

  // Функция для определения активного класса
  const getNavItemClass = (section) => {
    return `settings-nav-item ${activeSection === section ? 'active' : ''}`;
  };

  // Функция для отображения нужной секции
  const renderSection = () => {
    switch (activeSection) {
      case 'profile':
        return (
          <div className="settings-section">
            <h2 className="section-title">Профиль</h2>
            
            <div className="avatar-section">
              <div className="avatar-container">
                {profile?.avatar_url ? (
                  <img 
                    src={profile.avatar_url} 
                    alt="Avatar" 
                    className="avatar-image"
                  />
                ) : (
                  <div className="avatar-placeholder">
                    {user.email[0].toUpperCase()}
                  </div>
                )}
                <label className="avatar-upload-label">
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleAvatarChange}
                    disabled={uploading}
                    style={{ display: 'none' }}
                  />
                  <span className="avatar-upload-button">
                    {uploading ? 'Загрузка...' : 'Изменить'}
                  </span>
                </label>
              </div>
            </div>

            <div className="settings-group">
              <label>
                Отображаемое имя
                <input
                  type="text"
                  value={settings.display_name}
                  onChange={(e) => handleSettingChange('display_name', e.target.value)}
                  placeholder="Как вас называть?"
                  className="settings-input"
                />
              </label>
            </div>

            <div className="settings-group">
              <label>
                О себе
                <div className="input-counter">
                  <textarea
                    value={settings.bio}
                    onChange={(e) => {
                      if (e.target.value.length <= 2056) {
                        handleSettingChange('bio', e.target.value);
                      }
                    }}
                    placeholder="Расскажите о себе..."
                    className="settings-textarea"
                    rows="4"
                  />
                  <span className="char-counter">
                    {settings.bio?.length || 0}/2056
                  </span>
                </div>
              </label>
            </div>

            <div className="settings-group">
              <label>Email</label>
              <div className="settings-value">{user.email}</div>
            </div>
          </div>
        );

      case 'preferences':
        return (
          <div className="settings-section">
            <h2 className="section-title">Предпочтения</h2>
            
            <div className="settings-group">
              <label>
                Часовой пояс
                <select
                  value={settings.time_zone}
                  onChange={(e) => handleSettingChange('time_zone', e.target.value)}
                  className="settings-select"
                >
                  <option value="UTC">UTC</option>
                  <option value="Europe/Moscow">Europe/Moscow</option>
                  {/* Добавьте другие часовые пояса */}
                </select>
              </label>
            </div>

            <div className="settings-group">
              <label>
                Ежедневная цель (часов)
                <input
                  type="number"
                  value={settings.daily_goal / 60}
                  onChange={(e) => handleSettingChange('daily_goal', e.target.value * 60)}
                  min="1"
                  max="24"
                  className="settings-input"
                />
              </label>
            </div>

            <div className="settings-group">
              <label>
                Тема
                <select
                  value={settings.theme}
                  onChange={(e) => handleSettingChange('theme', e.target.value)}
                  className="settings-select"
                >
                  <option value="dark">Тёмная</option>
                  <option value="light">Светлая</option>
                </select>
              </label>
            </div>

            <div className="settings-group">
              <label>
                Язык
                <select
                  value={settings.language}
                  onChange={(e) => handleSettingChange('language', e.target.value)}
                  className="settings-select"
                >
                  <option value="en">English</option>
                  <option value="ru">Русский</option>
                </select>
              </label>
            </div>
          </div>
        );

      case 'notifications':
        return (
          <div className="settings-section">
            <h2 className="section-title">Уведомления</h2>
            
            <div className="settings-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={settings.notification_settings.daily_summary}
                  onChange={(e) => handleSettingChange('notification_settings.daily_summary', e.target.checked)}
                  className="settings-checkbox"
                />
                Ежедневный отчёт
              </label>
            </div>

            <div className="settings-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={settings.notification_settings.weekly_report}
                  onChange={(e) => handleSettingChange('notification_settings.weekly_report', e.target.checked)}
                  className="settings-checkbox"
                />
                Еженедельный отчёт
              </label>
            </div>

            <div className="settings-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={settings.notification_settings.goal_alerts}
                  onChange={(e) => handleSettingChange('notification_settings.goal_alerts', e.target.checked)}
                  className="settings-checkbox"
                />
                Уведомления о достижении целей
              </label>
            </div>

            <div className="settings-group">
              <label>
                Определение бездействия (минут)
                <select
                  value={settings.notification_settings.idle_detection}
                  onChange={(e) => handleSettingChange('notification_settings.idle_detection', Number(e.target.value))}
                  className="settings-select"
                >
                  <option value="5">5</option>
                  <option value="10">10</option>
                  <option value="15">15</option>
                  <option value="30">30</option>
                </select>
              </label>
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="page-container settings-page">
      <div className="page-header">
        <h1>Настройки аккаунта</h1>
        <p className="page-description">
          Управление настройками вашего аккаунта
        </p>
      </div>
      
      <div className="settings-wrapper">
        <div className="settings-content">
          {renderSection()}
        </div>

        <div className="settings-actions">
          <button 
            className="settings-save" 
            onClick={handleSaveSettings}
            disabled={saving}
          >
            {saving ? 'Сохранение...' : 'Сохранить изменения'}
          </button>
        </div>
      </div>
    </div>
  );
}

export default Account; 