import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { supabase } from '../config/supabase';
import LoadingScreen from '../components/LoadingScreen';
import './Auth.css';

function ResetPassword() {
  const navigate = useNavigate();
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(true);
  const [isTokenValid, setIsTokenValid] = useState(false);

  useEffect(() => {
    const handleRecoveryToken = async () => {
      try {
        // Проверяем наличие ошибок в URL
        const hashParams = new URLSearchParams(window.location.hash.substring(1));
        const errorType = hashParams.get('error');
        const errorDescription = hashParams.get('error_description');

        if (errorType) {
          let errorMessage;
          switch (errorType) {
            case 'access_denied':
              if (hashParams.get('error_code') === 'otp_expired') {
                errorMessage = 'Срок действия ссылки для сброса пароля истек. Пожалуйста, запросите новую ссылку.';
              } else {
                errorMessage = 'Доступ запрещен. Ссылка недействительна.';
              }
              break;
            default:
              errorMessage = errorDescription || 'Произошла ошибка при обработке ссылки.';
          }
          setError(errorMessage);
          setLoading(false);
          setTimeout(() => {
            navigate('/forgot-password');
          }, 5000);
          return;
        }

        // Проверяем наличие токена доступа
        const accessToken = hashParams.get('access_token');
        const refreshToken = hashParams.get('refresh_token');

        if (accessToken) {
          // Устанавливаем сессию с полученным токеном
          const { error: sessionError } = await supabase.auth.setSession({
            access_token: accessToken,
            refresh_token: refreshToken
          });

          if (sessionError) throw sessionError;
          setIsTokenValid(true);
        } else {
          throw new Error('Invalid recovery link');
        }
      } catch (err) {
        console.error('Error handling recovery token:', err);
        setError('Ошибка при обработке ссылки для сброса пароля. Пожалуйста, запросите новую ссылку.');
        setTimeout(() => {
          navigate('/forgot-password');
        }, 5000);
      } finally {
        setLoading(false);
      }
    };

    handleRecoveryToken();
  }, [navigate]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setMessage('');
    setLoading(true);

    if (password !== confirmPassword) {
      setError('Пароли не совпадают');
      setLoading(false);
      return;
    }

    try {
      const { error } = await supabase.auth.updateUser({
        password: password
      });

      if (error) throw error;

      setMessage('Пароль успешно изменен. Сейчас вы будете перенаправлены на страницу входа.');
      await supabase.auth.signOut();
      setTimeout(() => {
        navigate('/login');
      }, 3000);
    } catch (err) {
      console.error('Error resetting password:', err);
      setError(err.message || 'Ошибка при изменении пароля');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <LoadingScreen />;
  }

  if (error) {
    return (
      <div className="auth-page">
        <div className="auth-container">
          <h1>Ошибка сброса пароля</h1>
          <div className="auth-error">{error}</div>
          <p className="auth-description">
            Вы будете перенаправлены на страницу восстановления пароля...
          </p>
        </div>
      </div>
    );
  }

  if (!isTokenValid) {
    return null;
  }

  return (
    <div className="auth-page">
      <div className="auth-container">
        <div className="header-container">
          <button className="back-button" onClick={() => navigate('/login')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M15 18L9 12L15 6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
          <h1>Set new password</h1>
        </div>
        <p className="auth-description">Enter your new password</p>
        
        {error && <div className="auth-error">{error}</div>}
        {message && <div className="auth-success">{message}</div>}
        
        <form className="auth-form" onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="password">New Password</label>
            <input 
              type="password" 
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Enter new password"
              required 
              minLength={6}
            />
          </div>

          <div className="form-group">
            <label htmlFor="confirmPassword">Confirm Password</label>
            <input 
              type="password" 
              id="confirmPassword"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="Confirm new password"
              required 
              minLength={6}
            />
          </div>

          <button 
            type="submit" 
            className="auth-submit"
            disabled={loading}
          >
            {loading ? 'Updating...' : 'Update password'}
          </button>
        </form>
      </div>
    </div>
  );
}

export default ResetPassword; 