import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import './Auth.css';

function ForgotPassword() {
  const navigate = useNavigate();
  const { resetPassword } = useAuth();
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setMessage('');
    setLoading(true);

    try {
      const result = await resetPassword(email);
      if (result.success) {
        setMessage('Инструкции по восстановлению пароля отправлены на ваш email');
        setTimeout(() => navigate('/login'), 5000); // Редирект через 5 секунд
      } else {
        setError(result.error || 'Произошла ошибка при отправке письма');
      }
    } catch (err) {
      console.error('Reset password error:', err);
      setError('Не удалось отправить письмо для восстановления пароля');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-page">
      <div className="auth-container">
        <div className="header-container">
          <button className="back-button" onClick={() => navigate(-1)}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M15 18L9 12L15 6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
          <h1>Reset password</h1>
        </div>
        <p className="auth-description">Enter your email to reset your password</p>
        
        {error && <div className="auth-error">{error}</div>}
        {message && <div className="auth-success">{message}</div>}
        
        <form className="auth-form" onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input 
              type="email" 
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter your email"
              required 
            />
          </div>

          <button 
            type="submit" 
            className="auth-submit"
            disabled={loading}
          >
            {loading ? 'Sending...' : 'Send reset instructions'}
          </button>
        </form>

        <div className="auth-links">
          <div className="auth-separator">
            Remember your password?{' '}
            <Link to="/login" className="auth-link">
              Log in
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ForgotPassword; 